<script>
	import PiperNet from './templates/Hypns/PiperNet.svelte';
	import { validatePubKey } from '$lib/utils/index';

	export let openHypns;

	let contacts;
	function handleLoadContacts(e) {
		// get props fron e.detail, iterate through each contact, and update the contact's props
		contacts = e.detail
			.map((contact) => {
				const vpk = validatePubKey(contact.pubkey);
				if (!vpk) return null;

				return { pubkey: vpk, handle: contact.handle };
			})
			.filter((contact) => contact);

		console.log('Piper Manager handleLoadContacts', contacts);
	}
</script>

{#if contacts}
	<div class="m-2 p-2 border bg-slate-200 rounded min-h-[100px]">
		{#each contacts as contact}
			<div class="main m-1 p-2 rounded bg-neutral-800 text-slate-50 break-words">
				<PiperNet pubkey={contact.pubkey} handle={contact.handle} {openHypns} />
			</div>
		{/each}
	</div>
{/if}

<slot {handleLoadContacts} />
